}
.switch {
  white-space: nowrap;
  position: relative;
  display: inline-block;
  width: 60px;
  height: 100%;
  outline: none;
}
.switch input {
  position: absolute;
  top: -99999px;
  left: -99999px;
}
.slider {
  font-family: "Poppins", sans-serif;
  white-space: nowrap;
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s; /* ff3.6+ */ /* safari4+,chrome */ /* safari5.1+,chrome10+ */ /* opera 11.10+ */ /* ie10+ */
  background: linear-gradient(45deg, rgba(250,246,240,1) 0%, rgba(204,229,237,1) 100%); /* w3c */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#CCE5ED', endColorstr='#FAF6F0',GradientType=1 ); /* ie6-9 */ 
}
.slider p{
  top: 35%;
}
.slider:before {
  font-family: "Poppins", sans-serif;
  position: absolute;
  white-space: nowrap;
  content: "";
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
  margin-right: auto;
  height: 100%;
  width: 34px;
  left:0px;
  bottom: 0;
  top: 0;
  background-color: darkgray;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 5px;
}

input:checked + .slider {
  background-color: #ccc;
}
input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}
input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}
